:root{
  --error : #ff5d5d;
  --done: #3be73b;
  --black : #101010;
  --dark-grey : #202020;
  --grey: #404040;
  --light-grey: #606060;
  --dark-white : #afafaf;
  --white : #efefef;
  --inner-height: 100vh;
  --vh: 100;
}

html{
  color:var(--white);
  height: -webkit-fill-available;
}

html,body,
div,p,h1,
h2,h3,h4,
ul,li,a,
figure,
button,
input,
form,img{
  margin:0;
  padding: 0;
  border:0;
  box-sizing: border-box; 
}

html,
body,
#root{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-height: 100vh;
  height: 100%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-display:swap;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  min-height: -webkit-fill-available;
  background-color: var(--white);
  overflow-y: scroll;
}

#root{
  margin-bottom: 9vh;
}

li{
  list-style-type: none;
}

a {
  line-height: .85;
  text-decoration:none;
}

button:hover{
  cursor:pointer;
}

input{
  padding:0 0 .65rem 0;
  margin-bottom:1rem;
  border-radius:3px;
  border: 1px solid #e2e2e2;
  -webkit-border-radius:3px;
  -moz-border-radius:3px;
  -ms-border-radius:3px;
  -o-border-radius:3px;
}

input[type=text]:focus,
input[type=password]:focus,
input[type=email]:focus{
  color:var(--dark-grey);
  outline: none;
  transition: 45ms;
  -webkit-transition: 45ms;
  -moz-transition: 45ms;
  -ms-transition: 45ms;
  -o-transition: 45ms;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Components*/
.header{
  position:fixed;
  top:0;
  left:0;
  right:0;
  padding: 2vh 9% 2vh 9%;
  text-align: center;
  color: var(--grey);
  background-color: #afafaf;
  z-index:10;
}

.header .header__inner--right{
  width: 22%;
}

.footer{
  display:flex;
  align-items: flex-start;
  background-color: #202020;
  background-color: var(--dark-grey);
  position:fixed;
  bottom:0;
  right:0;
  left:0;
  padding: 2vh 9% 2vh 9%;
  transition: height 800ms ease;
  -webkit-transition: height 800ms ease;
  -moz-transition: height 800ms ease;
  -ms-transition: height 800ms ease;
  -o-transition: height 800ms ease;
}

.arrow {
  margin-top: 3px;
  color: var(--dark-grey);
  font-size: 4vh;
}

.social__icons--list{
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  width:80%;
  margin:0 auto;
}

/*cards counter*/

.cards__counter{
  font-size: 6vw;
  color: var(--dark-grey)
}

/* return */ 
.return__block {
  display:flex;
  justify-content: center;
  width:90%;
  margin: 0 auto;
  cursor: pointer;
}

.return__block svg{
  height:4vh;
  width: 4vh;
}

.return__icon{
  color: var(--dark-grey);
}

.google__icon{
  font-size: 1.5rem;
}

.facebook__icon{
  font-size: 1.25rem;
}

.toolbar{
  position: absolute;
  top: 0;
  right: 0;
  padding: .5rem;
  height: 7rem;
  width:40px;
  background-color: #151515;
  opacity: .90;
  border-top-right-radius: 4px;
}

input.toolbar__icon{
  margin:0;
  padding:0;
  border:none;
  background-color: transparent;
}

input.toolbar__icon:focus{
  color: rgb(59, 231, 59);
}

.toolbar__icon{
  font-size: 1.25rem;
  color: rgb(59, 231, 59);
  cursor: pointer;
}

.toolbar__icon svg{
  pointer-events: none;
}

.page{
  display:block;
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align:center;
}

.decks.page{
  justify-content: flex-start;
}

.subdeck.page{
  justify-content: flex-start;
  height:80vh;
}

.page--container{
  position: relative;
  height: calc(65 * var(--vh));
  width:100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.page__deck{
  background-color: var(--white);
}

.empty__container{
  width: 100%;
  height: calc(100% - 12vh);
}

/* popup */
.popup__container{
  position: fixed;
  top:0;
  left:0;
  right: 0;
  bottom:0;
  background-color:rgba(46, 46, 46, .95);
  z-index: 30;
  overflow-y: scroll;
}

.popup__inner{
  width: 80%;
  height: 90%;
  background-color: var(--dark-grey);
  overflow-y: scroll;
}

.popup__close{
  width: 4vh;
  height: 4vh;
  cursor: pointer;
}

.popup__close svg{
  width: 100%;
  height: 100%;
  color:rgb(59, 231, 59);
}

.popup__inner--container{
  display:flex;
  flex-direction: column;
  justify-content: center;
  height: 110%;
  width: 100%;
}

.popup__heading{
  display: flex;
  justify-content: flex-end;
  width: 80%;
  margin: 0 auto;
}

.popup__option--name{
  margin: 2vh 0;
}

.popup__option--input{
  margin: 0 2vh 0 0;
  cursor: pointer;
}

.popup__form--section{
  width: 80%;
  margin: 0 auto;
}

.popup__option--container{
  display: flex;
  justify-content: space-between;
  margin: 1vh auto;
  width: 85%;
}

.popup__option--name{
  text-align: left;
}

.btn.popup__button{
  margin: 2vh auto;
  background-color:rgb(59, 231, 59);
  color: var(--dark-grey);
  padding:3vw 0;
  width:69%;
}

/* profile page */
.profile__page{
  display: block;
  width: 100%;
  margin-bottom: 6vh;
  overflow-y: scroll;
} 

.profile__heading{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  color:var(--dark-grey);
  border: 1px #202020 dashed;
  border-radius: 12px;
  font-size: 20px;
  cursor: pointer;
  padding: 1rem;
}

.profile__avatar{
  height:20vh;
  width: 20vh;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}

.profile__avatar--button{
  position: absolute;
  right: 12px;
  bottom: 12px;
  cursor: pointer;
}

.profile__username{
  font-size:3.5vh;
}

.profile__userid{
  font-size:3vh;
}


/* deck list */
.deck__list{
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 65%;
  margin-bottom: 2rem;
}

.deck__list--empty{
  text-align: center;
  color: #202020;
}

/* deck */
.deck__name{
  color: var(--dark-grey);
  text-align: center;
}

.buttons__block{
  display: flex;
  flex-direction:column;
  margin:0 auto;
  width: 100%;
}



.form{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  width:100%;
  padding:4vh 0 4vh 0rem;
  color:var(--dark-grey);
}

.deck.form{
  height:0;
  padding:0;
  opacity: 0;
  overflow-y: hidden;
  transition: all .500s ease;
  -webkit-transition: all .500s ease;
  -moz-transition: all .500s ease;
  -ms-transition: all .500s ease;
  -o-transition: all .500s ease;
}

.deck__after--link{
  position:absolute;
  top:8vh;
  right:10vw;
}

.deck.form.isOpen{
  height: 584px;
  opacity: 1;
  transition: all .500s ease;
  -webkit-transition: all .500s ease;
  -moz-transition: all .500s ease;
  -ms-transition: all .500s ease;
  -o-transition: all .500s ease;
}

.form--container{
  width:100%;
  overflow-y: scroll; /* has to be scroll, not auto */
  -webkit-overflow-scrolling: touch;
}

.form--input{
  outline:none;
  margin-bottom: 2vh;
  background-color: var(--white);
  font-size: 1.125rem;
  color:var(--dark-grey);
  border:none;
  border-left: 2px solid transparent;
  border-right: 2px solid transparent;
  border-bottom: 2px solid var(--dark-grey);
}

.form__label{
  color: var(--dark-grey);
  padding-bottom: .25rem;
}

.form__label--avatar{
  cursor: pointer;
}

.form__label--textarea{
  color:var(--dark-grey);
  width:100%;
  font-size: 1.125rem;
}

.form__textarea{
  width:97%;
  resize:none;
  overflow: auto;
  height: 20vh;
  font-size: 16px;
}

.form__checkbox{
  margin:0 1vh 0 0;
}

.form--input:last-child{
  margin-bottom:4vh;
}

.form__radio--button{
  line-height: 1.125;
  margin-right: 1rem;
}

.form__radio--label{
  margin-right: .5rem;
  color:var(--dark-grey);
}

.form--input:focus{
  border:none;
  outline:none;
  border-left: 2px solid transparent;
  border-right: 2px solid transparent;
  border-bottom: 2px solid var(--dark-white);
  color:var(--white);
}

.form--section{
  display: flex;
  flex-direction:column;
  width: 80%;
}

.form__section--title{
  margin-bottom: .75rem;
  font-size: 2.75vh;
  color:var(--dark-grey);
}

.form--separator{
  line-height:0.1rem;
  background-color: #bbbbbb;
  text-align: center;
  width:80%;
  margin:2rem auto;
}

.form--separator-text{
  position:relative;
  top: -3px;
  padding:0 20px;
  color: var(--dark-grey);
  background-color: var(--white);
}

.search__bar{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding:0 1rem;
  color:var(--grey);
  width: 98%;
}

.search__bar--input{
  margin: 0;
  padding: .75rem 8vh .75rem .75rem;
  font-size: 3vh;
  width:100%;
  border: 1px solid var(--dark-white);
  background-color: transparent;
  border-radius:4px;
  -webkit-border-radius:4px;
  -moz-border-radius:4px;
  -ms-border-radius:4px;
  -o-border-radius:4px;
  text-align: center;
}

.search__bar--icon{
  position: absolute;
  right: 1.5rem;
  font-size:5.5vh;
  z-index:1;
  cursor: pointer;
}


.action {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size:1.25rem;
  color:var(--white)
}

/* drop down component */
.dropdown__menu{
  color:var(--white);
  width:80%;
  margin: 0 auto;
}

.dropdown__draw{
  background-color:#151515;
  font-size: 4vh;
  margin-bottom:1rem;
}


.dropdown__link{
  display: block;
  padding-top:1rem;
  margin-bottom:.5rem;
  height: 50px;
  color: var(--white);
  font-size: calc(3 * var(--vh));
  margin-left:1rem;
}

.dropdown__body{
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height:0;
  height:0;
  overflow: hidden;
  transition:min-height 1s ease;
  -webkit-transition:min-height 1s ease;
  -moz-transition:min-height 1s ease;
  -ms-transition:min-height 1s ease;
  -o-transition:min-height 1s ease;
  background-color: #404040;
}

.dropdown__content{
  width: 100%;
  padding:0 1rem;
  margin-bottom:.75rem;
  font-size: calc(1.75 * var(--vh));
}

.dropdown__cta--box{
  display: flex;
  justify-content: space-between;
  padding:0 1rem;
  width:100%;
}

.dropdown__icon{
  cursor: pointer;
}

svg.dropdown__icon path{
  stroke:var(--white);
}

.dropdown__draw:target .dropdown__body{
  min-height: calc(12 * var(--vh));
  transition:min-height 1s ease;
  -webkit-transition:min-height 1s ease;
  -moz-transition:min-height 1s ease;
  -ms-transition:min-height 1s ease;
  -o-transition:min-height 1s ease;
  padding:.25rem;
}

/* pagination component */
.pagination__block{
  width: 80%;
  font-size: 1.25rem;
}

.pagination__text--block{
  width : 35%;
  line-height: 1.25;
}

.pagination__text{
  color: var(--black);
  cursor: pointer;
}

.pagination__arrow{
  color:var(--black);
  cursor: pointer;
}

/* filters */
.filter__container{
  width: 83%;
  color:var(--black);
  cursor: pointer;
  height: 4vh;
  z-index: 1;
}

.filter__container svg{
  height: 100%;
  width: 4vh;
}

/* decks list*/
.list__content{
  height:55vh; 
}

.subdeck.list__content{
  height:65vh;
}

/* deck */

.deck__block{
  display: flex;
  justify-content: center;
  width:100%;
  background-color:var(--dark-grey);
  margin:.5vh auto;
  padding:.5rem 1.5rem;
  min-height: 100px;
  border-radius:2px;
  -webkit-border-radius:2px;
  -moz-border-radius:2px;
  -ms-border-radius:2px;
  -o-border-radius:2px;
}


.deck__inner--left{
  width: 60%;
  margin-right:1.5rem;
}

.deck__inner--right{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width:30%;
}
.inner__right--text{
  display:flex;
  justify-content: space-between;
  align-items: flex-end;
}

.deck__ec--title{
  font-size: 1.125rem;
  margin-right: 1rem;
}

.deck__ec--value{
  font-size: 1.25rem;
}

.deck__infos{
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 40%;
}

.inner__right--actions{
  display: flex;
  justify-content: space-between;
  height:30%;
  width: 100%;
}

/* avatar */
.avatar, .logout{
  height:4vh;
  width: 4vh;
}

.avatar svg, .logout svg {
  width: 100%;
  height: 100%;
}

/* subdeck */
.subdeck__box{
  width: 80%;
}

.subdeck__heading{
  background-color: var(--dark-grey);
}

.subdeck__body{
  display:grid;
  grid-template-columns: repeat(3,1fr);
  border: 1px solid black;
  grid-auto-rows: minmax(112px, auto);
  gap:12px;
}

/*cards*/

.card__container{
  position:relative;
  width:100%;
}

.card__image{
  width: 100%;
  border-radius:4px;
  -webkit-border-radius:4px;
  -moz-border-radius:4px;
  -ms-border-radius:4px;
  -o-border-radius:4px;
}

.card__checkbox{
  position: absolute;
  right: calc(1 * var(--vh));
  top: calc(1 * var(--vh));
  transform: scale(1.15);
  -webkit-transform: scale(1.15);
  -moz-transform: scale(1.15);
  -ms-transform: scale(1.15);
  -o-transform: scale(1.15);
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
}

.card__thumbnail--container{
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    color: var(--dark-grey);
    font-size: 3vh;
    cursor:pointer;
}

.card__thumbnail--container svg {
  pointer-events:none;
}

.subdeck__type{
  color: var(--white);
  font-size: 4vw;
  text-transform: uppercase;
}

.subdeck__infos{
  color: var(--black);
  font-size: 2vh;
}

.subdeck .layout{
  width: 90%;
  height: 100%;
  background-color: transparent;
}

.subdeck__icon--list{
    width: 30%;
}

.subdeck__icon{
  font-size: 4vh;
  color: var(--dark-grey);
  cursor: pointer;
}

.subdeck .popup__body{
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}

.subdeck .popup__footer{
  width: 100%;
}

.loader__block{
  background-color: rgba(46, 46, 46, .95);
  width:100vw;
  height: 100vh;
  z-index: 40;
}

.loader{
  height: 30vw;
  width: 30vw;
  color:rgb(59, 231, 59);
  animation: loading 1s  linear infinite;
  -webkit-animation: loading 1s  linear infinite;
}

.loader__image{
   height: 2vh;
}

