/* Layouts */
.layout{
  display:grid;
  position: relative;
  background-color: var(--white);
  width:90%;
  overflow-y:scroll;
  overflow-x: hidden;
  gap:12px 16px;
  margin:0 auto;
  justify-content: center;
  align-items: center;
  padding:1rem 1rem;
}

.layout::after{
  content:"";
  height:.1rem;
}

.layout__1{
  display:flex;
  flex-direction: column;
  justify-content: flex-start;
}

.layout__2{
    grid-template-columns: repeat(2,1fr);
}

.layout__3{
    grid-template-columns: repeat(3,1fr);
}

.layout__4{
  grid-template-columns: repeat(4,1fr);
}

/* main */
main {
  display: flex;
  flex-direction: column;
  margin-bottom: 4vh;
}

/* Media queries */

@media screen and (min-width: 480px){

}

@media screen and (min-width: 640px){
  .btn{
      padding:1.5vw 0;
      font-size:3vw;
  }
}

@media screen and (min-width: 920px){
  .btn{
    padding:1.125vw 0;
    font-size:2.25vw;
  }
}

@media screen and (min-width: 1240px){
